import './App.css';
import React, {useEffect, useState} from 'react';
import firebase from "firebase"

function makeid(length) {
  var result           = '';
  var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  var charactersLength = characters.length;
  for ( var i = 0; i < length; i++ ) {
     result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
}

function App() {
    const [text,setText] = useState("")
    const [id, setId] = useState()
    useEffect(()=>{
      
      if(window.location.search) 
      firebase.database().ref("shortlinks/"+window.location.search.replace("?","")).on("value", (snapshot)=>{
        snapshot.val().slice(0,4)=="http"?
        window.location.assign(snapshot.val()):window.location.assign("https://"+snapshot.val())
      })

    }, [text])

    function generate(){
        var temp = makeid(6);
        setId(temp);
        firebase.database().ref("shortlinks/"+temp).set(text)

    }
      
  return (
    <div className="App">
      <header className="App-header">
            <h1>ShortLink</h1>
           <input className="App-link" value={text} onChange={(value)=>{setText(value.target.value)}} type="search"/>
           <div className="buttom" onClick={()=>{generate()}}>
           Enter
           </div>
           {id?
            <div>
              <a href={window.location.origin+"/"+id}>{window.location.origin+"/?"+id}</a>
            </div>:null}
      </header>
    </div>
  );
}

export default App;
